body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Raleway', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.recipes-container {
  display: grid;
    grid-template-columns: repeat(auto-fit, 254px);
    grid-gap: 20px;
    justify-content: center;
    margin: 30px auto;
}

.no-favorites {
  text-align: center; 
  font-size: 25px;
  color: #3D323E;
  font-weight: 700;
  margin-top: 150px;
}

.sorry {
  text-align: center;
  font-family: 'Raleway';
  font-size: 30px;
  color: #3D323E;
  margin-top: 150px;
}
.recipe-card {
  border: .5px solid black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 400px;
  width: 250px;
  margin: 5px 5px 100px 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 0;
}

.recipe-card::after {
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0) 100%);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0) 100%)
}

a {
  text-decoration: none;
  color: #3D323E;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.favorites-box {
  position: absolute;
  top: 350px;
  left: 20px;
  width: 200px;
  height: 100px;
  background: #FFF;
  border: 4px solid #32636E;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.recipe-name {
  font-family: Raleway;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  font-size: 15px;
  color: #3D323E;
  text-align: center;
  margin: 20px 0 10px 0;
}

.recipe-name:hover {
  cursor: pointer;
  color: #D5AD47;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.fa-heart {
  margin: 0 auto;
  font-size: 30px;
  margin: 5px 0 10px 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.fa-heart:hover {
  cursor: pointer;
  color: #D5AD47;
}

.fas {
  color: #D5AD47;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.gradient {
  background: -webkit-linear-gradient(bottom, rgba(225,225,225,1) 0%,rgba(225,225,231,0.3) 41%,rgba(255,255,255,0) 100%);
  background: linear-gradient(to top, rgba(225,225,225,1) 0%,rgba(225,225,231,0.3) 41%,rgba(255,255,255,0) 100%);
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: -1;
  
}
.loading-container {
  text-align: center;
  margin-top: 150px;
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #3D323E;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #3D323E transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading {
  text-align: center;
  font-family: 'Raleway';
  font-size: 30px;
  color: #3D323E;
  margin-top: 0;
  -webkit-animation:blinkingText 0.8s infinite alternate;
          animation:blinkingText 0.8s infinite alternate;
}

@-webkit-keyframes blinkingText{
  0%{     opacity: 0;    }
  25%{    opacity: .25; }
  50%{    opacity: .5; }
  75%{    opacity: .75;  }
  100%{   opacity: 1;    }
}

@keyframes blinkingText{
  0%{     opacity: 0;    }
  25%{    opacity: .25; }
  50%{    opacity: .5; }
  75%{    opacity: .75;  }
  100%{   opacity: 1;    }
}

input {
  background-color: #D5AD47;
  padding-left: 20px;
  height: 47px;
  border: none;
  font-size: 17px;
  /* width: 99.5%; */
}

input:hover {
  cursor: pointer;
}

input::-webkit-input-placeholder {
  color: #32636E;
}

input::-ms-input-placeholder {
